<!--  -->
<template>
    <div>这是聊天页面</div>

    <!-- <input type="text"> -->

    <div>
        <el-scrollbar max-height="500px" style="width: 800px;display: inline-block;">
            <div>
                <p v-for="item in message" :key="item"
                    :class="item.uuid == this.$route.query.uuid ? 'scrollbar-demo-item2' : 'scrollbar-demo-item'">
                    <img src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"
                        :class="item.uuid == this.$route.query.uuid ? 'pic2' : 'pic'">
                <div :class="item.uuid == this.$route.query.uuid ? 'container2' : 'container'">
                    <div :class="item.uuid == this.$route.query.uuid ? 'account2' : 'account'">{{ item.account }}</div>
                    <p :class="item.uuid == this.$route.query.uuid ? 'msg2' : 'msg'">{{ item.message }}</p>
                </div>
                </p>
                <!-- 
                <p v-for="item in count" :key="item" class="scrollbar-demo-item2" v-show="this.$route.query.account">
                    <img src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png" class="pic2">
                <div class="container2">
                    <div class="account2">{{ this.$route.query.account }}</div>
                    <p class="msg2">{{ item }}</p>
                </div>
                </p> -->

            </div>
        </el-scrollbar>
    </div>

    <el-input style="width: 530px;" @keydown.enter="submit" v-model="input" placeholder="Please input" />
    <el-button type="primary" @click="submit">提交</el-button>
</template>

<script>
import io from 'socket.io-client';
import { ElInput } from 'element-plus';
import { ref } from 'vue';


export default {
    name: 'chat',
    data() {
        return {
            input: '',
            count: ['这里是用户发送的信息之一，内容长度可以随意变化'],
            message: []
        }
    },
    mounted() {
        console.log('挂载完毕')

        const socket = io('http://localhost:3030');
        this.getMsg(socket)

    },
    components: {
        ElInput
    },
    methods: {
        submit() {
            if (this.input.length == 0) alert('内容不可以为空')
            else {
                this.submitMsg(this.input)
                this.input = '';
            }

        },
        async getMsg(socket) {
            // const socket = io('http://localhost:3030');


            // 发送消息给服务器
            // await new Promise ((resolve)=>{
            //    socket.emit('message', this.input,resolve); 
            // })
            socket.on('connect', () => {
                console.log('connected to server');
            });

            socket.on('allMessage', (res) => {
                this.message = res
            })

            const result = await new Promise((resolve) => {
                socket.emit('accountAndUuid', [this.$route.query.account, this.$route.query.uuid], (data) => {
                    resolve(data)
                })
            })
            console.log('result=>', result)



            // socket.disconnect();
        },
        async submitMsg(msg) {
            const socket = io('http://localhost:3030');

            var data = {
                account: this.$route.query.account,
                uuid: this.$route.query.uuid,
                message: msg
            };
            const result = await new Promise((resolve) => {
                socket.emit('submitMsg', data, (res) => {
                    resolve(res)
                })
            });
            this.message = result
            console.log(this.message)

            socket.disconnect();
        }
    }
}
</script>

<style scoped>
.scrollbar-demo-item {
    display: flex;
    text-align: left;
    height: 50px;
    margin: 10px;
    border-radius: 4px;
    background: #fbf7f7;
    color: black;
}

.scrollbar-demo-item2 {
    text-align: right;
    height: 50px;
    margin: 10px;
    border-radius: 4px;
    background: #fbf7f7;
    color: black;
}

.pic {
    width: 50px;
    height: 50px;
    float: left;
}

.pic2 {
    width: 50px;
    height: 50px;
    float: right;
}

.account {
    height: 17px;
    font-size: 1px;
    line-height: 17px;
    margin-left: 10px;
    color: rgb(16, 101, 101);
}

.account2 {
    height: 17px;
    font-size: 1px;
    line-height: 17px;
    margin-left: 10px;
    color: rgb(16, 101, 101);
    padding-right: 10px;
}

.msg {
    height: 30px;
    margin: 0 0;
    font-size: 18px;
    display: inline-block;
    margin-left: 10px;
    border-radius: 4px;
    background-color: #ebcbcb;
    line-height: 30px;
}

.msg2 {
    height: 30px;
    margin: 0 0;
    font-size: 18px;
    display: inline-block;
    margin-left: 10px;
    border-radius: 4px;
    line-height: 30px;
    padding-right: 10px;
}

.container {
    height: 50px;
    display: flex;
    flex-direction: column;
}

.container2 {
    height: 50px;
    display: flex;
    flex-direction: column;
}
</style>