<!--  -->
<template>
    <div>这是主页面</div>

    <!-- Form -->
    <el-button text @click="dialogFormVisible = true">
        进入聊天室
    </el-button>

    <el-dialog v-model="dialogFormVisible" title="Before entering" style="width: min-content;">
        <el-form :model="form">
            <el-form-item label="自定义一个用户名" :label-width="formLabelWidth">
                <el-input v-model="form.name" autocomplete="off" />
            </el-form-item>
        </el-form>
        <template #footer>
            <span class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取消</el-button>
                <el-button type="primary" @click="goToChat">
                    确认
                </el-button>
            </span>
        </template>
    </el-dialog>


    <router-link to="/about">跳转关于</router-link>
</template>

<script>
import { io } from 'socket.io-client';
import { v4 as uuidv4 } from 'uuid';


export default {
    name: 'home',
    data() {
        return {
            form: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: '',
            },
            dialogFormVisible: false,
            allaccount: [],
            uuid: ''
        }
    },
    mounted() {

        this.getuuid()
    },
    methods: {
        goToChat() {

            if (this.form.name.length == 0) {
                alert('用户名不可为空');
                return
            }
            this.dialogFormVisible = false;
            this.$router.push({ name: "chat", query: { account: this.form.name,uuid: this.uuid } })
        },
        getuuid() {
            const uuid = uuidv4();
            this.uuid=uuid;
        }
    }
}
</script>

<style scoped>
.el-button--text {
    margin-right: 15px;
}

.el-select {
    width: 100px;
}

.el-input {
    width: 300px;
}

.dialog-footer button:first-child {
    margin-right: 10px;
}
</style>